import logo1 from './media/swam.png';
import logo2 from './media/shotbytrip.png';
import logo3 from './media/topshooter.png';
import logo4 from './media/perfectinsight.png';
import logo5 from './media/papa.png';
import logo6 from './media/trip.png';
import ill1 from './media/holy.png';
import ill2 from './media/melo.png';
import ill3 from './media/onthatnote.png';
import ill4 from './media/ill1.png'
import './Illustrations.css';
const Illustrations = () => {

    return (

        <>
            <h1 className="welcomeTxt" style={{textAlign:'center'}}>Logos</h1>
            <div className="container" id="illustrationContainer">

                <img className="logos" src={logo1} />
                <img className="logos" src={logo2} />
                <img className="logos" src={logo3} />
                <img className="logos" src={logo4} />
                <img className="logos" src={logo5} />
                <img className="logos" src={logo6} />
            </div>
            <h1 className="welcomeTxt" style={{textAlign:'center'}}>Illustrations</h1> 
            <div className="container" id="illustrationContainer">
            <img className="logos" src={ill1} />
                <img className="logos" src={ill2} />
                <img className="logos" src={ill3} />
                <img className="logos" src={ill4} />
            </div>
        </>

    )
}
export default Illustrations;