import './Navbar.css';
import { Link } from 'react-router-dom';
import { useState } from 'react';
const Navabr = () => {
    const [clicked,setClicked] = useState(false)

    function isClicked(){
        setClicked(!clicked)
    }
    return (
        <>
            <nav>
                <Link className="logo" to="/">TariqSadu</Link>

                <div className="menu-toggle" onClick={isClicked}>
                    <div className={clicked ? 'bar active':'bar'}></div>
                    <div className={clicked ? 'bar active':'bar'}></div>
                    <div className={clicked ? 'bar active':'bar'}></div>
                </div>
                <ul className={clicked ? 'menu active' : 'menu'} id="menu">
                    <li className="menu-item">
                        <Link className="menu-link" id="first" to="/">Home</Link>
                    </li>
                    <li className="menu-item">
                        <Link className="menu-link" id="second" to="/projects">Projects</Link>
                    </li>
                    <li className="menu-item">
                        <a className="menu-link" id="third" href="https://riqsadu.myportfolio.com/portfolio">Photography</a>
                    </li>
                    <li className="menu-item">
                        <Link className="menu-link" id="fourth"to="/illustrations">Illustrations</Link>
                    </li>
                </ul>
            </nav>
        </>
    )
}
export default Navabr